let playState = function (game) {
    this.gameSpeed = 400;
    this.score = 0;

    this.init = function () {
        this.score = 0;
        //
        this.bg = game.add.tileSprite(0, 0, game.width, game.height, 'bg');

        //
        this.pipeGroup = game.add.group();
        this.pipeGroup.enableBody = true;
        this.createPipe = function (gap) {
            gap = gap || 100;
            var topPipeY = 100 + (game.height - 281) / 2 * Math.random() - 820; //
            var bottomPipeY = game.height - 281 - Math.abs(topPipeY) + gap; //

            var topPipe = game.add.sprite(game.width, topPipeY, 'pipe', 1, this.pipeGroup); //
            var bottomPipe = game.add.sprite(game.width, bottomPipeY, 'pipe', 0, this.pipeGroup); //
            this.pipeGroup.setAll('checkWorldBounds', true); //
            this.pipeGroup.setAll('outOfBoundsKill', true); //kill
            this.pipeGroup.setAll('body.velocity.x', -this.gameSpeed); //
        };

        //
        this.bird = game.add.sprite(game.world.centerX, game.world.centerY, 'bird');
        this.bird.anchor = {x: 0.5, y: 0.5};
        this.bird.animations.add('fly', [1, 0, 2]);
        this.bird.scale = new window.Phaser.Point(0.3, 0.3);
        game.physics.enable(this.bird, window.Phaser.Physics.ARCADE);
        this.bird.startFly = () => {
            this.bird.animations.play('fly', 10, true, true);
            this.bird.body.gravity.y = 1150;
        };
        this.bird.endFly = () => {
            this.bird.alive = false;
            this.bird.animations.stop('fly');
        };

        //
        this.ground = game.add.tileSprite(0, game.height - 281, game.width, 281, 'ground'); //
        game.physics.enable(this.ground, window.Phaser.Physics.ARCADE);
        this.ground.body.immovable = true;

        //
        this.scoreText = game.add.text(game.world.centerX,100,this.score,{
            fill:'#fff',
            fontSize:'48px'
        });
        this.scoreText.anchor = {x:0.5,y:0.5};

        this.checkScore = function(pipe){//,pipe
            //pipe.hasScored 
            //pipe.y<0
            //x x
            if(!pipe.hasScored && pipe.y<=0 && pipe.x<=this.bird.x-17-54){
                this.score++;
                pipe.hasScored = true;
                this.scoreText.text = this.score; //
                //this.soundScore.play(); //
                return true;
            }
            return false;
        };

        //
        var input = game.input; //input
        var signal = input.onDown; // Signal
        signal.add(() => {
            if(!this.bird.alive) return;
            this.game.add.tween(this.bird).to({angle: -20}, 100).start();
            this.bird.body.velocity.y = -350;
        }); //Signal 

        this.showEnd = () => {
            var scoreBg = game.add.image(game.world.centerX, game.world.centerY, 'scoreBg');
            scoreBg.anchor = {x:0.5,y:0.5};
            scoreBg.width = 400;
            scoreBg.height = 400;
            var btn = game.add.button(game.world.centerX,game.world.centerY+100,'restart',() => {
                game.state.restart('play');
            });
            btn.anchor.setTo(0.5,0.5);
            btn.width = 100;
            btn.height = 100;

            var text = game.add.text(game.world.centerX,game.world.centerY-108,this.score,{
                fill:'#ffcc00',
                fontSize:'48px'
            });
            text.anchor = {x:0,y:0.5};
        };

        this.beginGame = () => {
            this.bg.autoScroll(-this.gameSpeed, 0);//
            this.ground.autoScroll(-this.gameSpeed, 0);//
            this.bird.startFly();//
            this.timer = game.time.events.loop(3600, this.createPipe, this);//
        };
        this.endGame = () => {
            this.bg.stopScroll();
            this.ground.stopScroll();
            this.bird.endFly();
            this.pipeGroup.setAll('body.velocity.x', 0);
            this.game.time.events.remove(this.timer);
            this.showEnd();
        };
        this.beginGame();
    };
    this.update = function () { //update
        if (this.bird.angle < 20)
            this.bird.angle += 1;
        game.physics.arcade.collide(this.bird, this.ground, () => 1, null, this); //
        game.physics.arcade.overlap(this.bird, this.pipeGroup, this.endGame, null, this);
        this.pipeGroup.forEachExists(this.checkScore,this);
    };
    this.create = function () {

    }
};

export default playState;


